

@font-face {
  font-family: "Flaticon";
  src: url("../fonts/Flaticon.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}

[class^="flaticon-"]:before, [class*=" flaticon-"]:before,
[class^="flaticon-"]:after, [class*=" flaticon-"]:after {
  font-family: Flaticon;
  font-size: 20px;
  font-style: normal;
  margin-left: 20px;
}

.flaticon-magnifying-glass:before { content: "\f100"; }
.flaticon-menu:before { content: "\f101"; }
.flaticon-cloudy:before { content: "\f102"; }
.flaticon-twitter:before { content: "\f103"; }
.flaticon-facebook:before { content: "\f104"; }
.flaticon-instagram:before { content: "\f105"; }
.flaticon-pinterest-social-logo:before { content: "\f106"; }
.flaticon-youtube:before { content: "\f107"; }
.flaticon-linkedin:before { content: "\f108"; }

@mixin flex{
  display: flex;
  justify-content: center;
  align-items: center;
}
header{
  background-color: grey;
  height: 80px;
}
nav{
  width: 90%;
  margin: auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
#logo{
  width: 200px;
  color: #ff7a53;
}
.menu-icons{
  @include flex;
  flex-direction: row;
  div{
    @include flex;
    flex-direction: column;
  }
}
